import {AfterViewInit, Component, TemplateRef, ViewChild} from '@angular/core';
import {MatTableDataSource} from "@angular/material/table";
import {Skill, SkillsService, Talent, TalentsService} from "../../../api";
import {MatSort} from "@angular/material/sort";
import {MatDialog} from "@angular/material/dialog";

@Component({
  selector: 'app-skill-management',
  templateUrl: './skill-management.component.html',
  styleUrls: ['./skill-management.component.scss']
})
export class SkillManagementComponent implements AfterViewInit {
  displayedColumns: string[] = [ 'name', 'advanced', 'description', 'isCategory', 'actions'];
  dataSource: MatTableDataSource<Skill>;
  @ViewChild(MatSort) sort: MatSort;

  constructor(public skillsService: SkillsService, private dialog: MatDialog) {


  }

  ngAfterViewInit() {
    this.fetchData()
  }

  async fetchData() {
    const data = await this.skillsService.skillControllerFindAll().toPromise();
    console.log(data)
    this.dataSource = new MatTableDataSource(data);
    this.dataSource.sort = this.sort;

  }

  async delete(id: string) {
    await this.skillsService.skillControllerRemove(id).toPromise();
    await this.fetchData();
  }

  async openDialogWithTemplateRef(templateRef: TemplateRef<any>, data: any) {
    const result = await this.dialog.open(templateRef, {data}).afterClosed().toPromise();
    if (result) {
      await this.delete(result);
    }
  }


}
