import {AfterViewInit, Component, TemplateRef, ViewChild} from '@angular/core';
import {Talent, TalentsService} from "../../../api";
import {MatTableDataSource} from "@angular/material/table";
import {MatSort} from "@angular/material/sort";
import {MatDialog} from "@angular/material/dialog";

@Component({
  selector: 'app-talent-management',
  templateUrl: './talent-management.component.html',
  styleUrls: ['./talent-management.component.scss']
})
export class TalentManagementComponent implements AfterViewInit{
  displayedColumns: string[] = [ 'name', 'description', 'actions'];
  dataSource: MatTableDataSource<Talent>;
  @ViewChild(MatSort) sort: MatSort;

  constructor(public talentService: TalentsService, private dialog: MatDialog) {


  }

  ngAfterViewInit() {
    this.fetchData()
  }

  async fetchData() {
    const data = await this.talentService.talentControllerFindAll().toPromise();
    console.log(data)
    this.dataSource = new MatTableDataSource(data);
    this.dataSource.sort = this.sort;

  }

  async delete(id: string) {
    await this.talentService.talentControllerRemove(id).toPromise();
    await this.fetchData();
  }

  async openDialogWithTemplateRef(templateRef: TemplateRef<any>, data: any) {
    const result = await this.dialog.open(templateRef, {data}).afterClosed().toPromise();
    if (result) {
      await this.delete(result);
    }
  }

}
