import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import type { Observable } from 'rxjs';

import type { CreateCampaignDto } from '../models/CreateCampaignDto';
import type { UpdateCampaignDto } from '../models/UpdateCampaignDto';

import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

@Injectable()
export class CampaignsService {

    constructor(public readonly http: HttpClient) {}

    /**
     * @param requestBody
     * @returns any
     * @throws ApiError
     */
    public campaignsControllerCreate(
        requestBody: CreateCampaignDto,
    ): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'POST',
            url: '/api/campaigns',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns string
     * @throws ApiError
     */
    public campaignsControllerFindAll(): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/campaigns',
        });
    }

    /**
     * @param channelId
     * @param campaignCode
     * @returns any
     * @throws ApiError
     */
    public campaignsControllerUpdateChannel(
        channelId: number,
        campaignCode: string,
    ): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'PATCH',
            url: '/api/campaigns/update-channel',
            query: {
                'channelId': channelId,
                'campaignCode': campaignCode,
            },
        });
    }

    /**
     * @param id
     * @returns string
     * @throws ApiError
     */
    public campaignsControllerFindOne(
        id: string,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/campaigns/{id}',
            path: {
                'id': id,
            },
        });
    }

    /**
     * @param id
     * @param requestBody
     * @returns string
     * @throws ApiError
     */
    public campaignsControllerUpdate(
        id: string,
        requestBody: UpdateCampaignDto,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'PATCH',
            url: '/api/campaigns/{id}',
            path: {
                'id': id,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @param id
     * @returns string
     * @throws ApiError
     */
    public campaignsControllerRemove(
        id: string,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'DELETE',
            url: '/api/campaigns/{id}',
            path: {
                'id': id,
            },
        });
    }

}
