import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {LoginComponent} from "./auth/login/login.component";
import {HomeComponent} from "./dashboard/home/home.component";
import {IsLoggedInGuard} from "./auth/is-logged-in.guard";
import {TalentManagementComponent} from "./administration/talent-management/talent-management.component";
import {SkillManagementComponent} from "./administration/skill-management/skill-management.component";
import {ProfessionManagementComponent} from "./administration/profession-management/profession-management.component";
import {IsAdminGuard} from "./auth/is-admin.guard";
import {CreateTalentComponent} from "./administration/talent-management/create-talent/create-talent.component";
import {
  UpdateTalentComponent
} from "./administration/talent-management/update-talent/update-talent.component";


const administrativeRoutes: Routes = [
  { path : 'talents', component: TalentManagementComponent, children: [
      {path: 'create', component: CreateTalentComponent},
      {path: 'update/:id', component: UpdateTalentComponent}
    ]},
  { path : 'skills', component: SkillManagementComponent},
  { path : 'professions', component: ProfessionManagementComponent},
]


const guardedRoutes: Routes = [
  { path : '', component: HomeComponent },
  { path : 'administration', canActivate: [IsAdminGuard], children: administrativeRoutes },
]


const routes: Routes = [
  {path: 'login', component: LoginComponent},
  {path: '', canActivate:[IsLoggedInGuard], children: guardedRoutes}
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
