import { Injectable } from '@angular/core';
import {AuthService, loginDto, OpenAPI, User} from "../../api";
import {firstValueFrom} from "rxjs";
import jwt_decode from 'jwt-decode';
import {Router} from "@angular/router";

const TOKEN_STORAGE_KEY = 'AuthorizationToken'

@Injectable({
  providedIn: 'root'
})
export class UserService {

  public user?: User;
  public token?: string;

  constructor(private authService: AuthService, private router: Router) {}

  initJWT(){
    const token = localStorage.getItem(TOKEN_STORAGE_KEY)
    if (token) {
      const decodedToken = jwt_decode(token) as any
      if (decodedToken) {
        this.user = decodedToken.sub as User
        OpenAPI.TOKEN = token;
        this.token = token
      }

    }

  }

  async login(credentials: loginDto) {
    const token$ = this.authService.authControllerLogin(credentials)
    const token = await firstValueFrom(token$)

    if (token) {
      localStorage.setItem(TOKEN_STORAGE_KEY, token.access_token)
      this.initJWT()
      this.router.navigate(['/'])

    }
  }

  logout() {
    localStorage.removeItem(TOKEN_STORAGE_KEY)
    this.user = undefined;
    OpenAPI.TOKEN = ''
  }


}
