import {Component, EventEmitter, Output} from '@angular/core';
import {MatDialog} from "@angular/material/dialog";
import {SkillFormComponent} from "../skill-form/skill-form.component";
import {Subject} from "rxjs";

@Component({
  selector: 'app-create-skill',
  templateUrl: './create-skill.component.html',
  styleUrls: ['./create-skill.component.scss']
})
export class CreateSkillComponent {

  @Output()
  dataFromModal = new EventEmitter();

  constructor(public dialog: MatDialog) {}

  public async openCreateDialog(): Promise<void> {
    this.dialog.open(SkillFormComponent, {
      data: {mode: 'create', emitter: this.dataFromModal},
      width: '70%'
    });
  }
}
