import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import type { Observable } from 'rxjs';

import type { ExportedEnums } from '../models/ExportedEnums';

import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

@Injectable()
export class DefaultService {

    constructor(public readonly http: HttpClient) {}

    /**
     * @param requestBody
     * @returns boolean
     * @throws ApiError
     */
    public appControllerExportedEnums(
        requestBody: ExportedEnums,
    ): Observable<boolean> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns any
     * @throws ApiError
     */
    public appControllerTestBot(): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/test-bot',
        });
    }

    /**
     * @returns any
     * @throws ApiError
     */
    public appControllerUpdateCommands(): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/update-commands',
        });
    }

    /**
     * @returns any
     * @throws ApiError
     */
    public appControllerInteractions(): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'POST',
            url: '/api/interactions',
        });
    }

    /**
     * @returns any
     * @throws ApiError
     */
    public appControllerSendMessage(): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/send',
        });
    }

}
