import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import type { Observable } from 'rxjs';

import type { CreateHeroDto } from '../models/CreateHeroDto';
import type { Hero } from '../models/Hero';
import type { UpdateHeroDto } from '../models/UpdateHeroDto';
import type { User } from '../models/User';

import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

@Injectable()
export class HeroesService {

    constructor(public readonly http: HttpClient) {}

    /**
     * @param requestBody
     * @returns Hero
     * @throws ApiError
     */
    public heroControllerCreate(
        requestBody: CreateHeroDto,
    ): Observable<Hero> {
        return __request(OpenAPI, this.http, {
            method: 'POST',
            url: '/api/hero',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns Hero
     * @throws ApiError
     */
    public heroControllerFindAll(): Observable<Array<Hero>> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/hero',
        });
    }

    /**
     * @returns User
     * @throws ApiError
     */
    public heroControllerTest(): Observable<User> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/hero/test',
        });
    }

    /**
     * @param id
     * @returns string
     * @throws ApiError
     */
    public heroControllerFindOne(
        id: string,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/hero/{id}',
            path: {
                'id': id,
            },
        });
    }

    /**
     * @param id
     * @param requestBody
     * @returns string
     * @throws ApiError
     */
    public heroControllerUpdate(
        id: string,
        requestBody: UpdateHeroDto,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'PATCH',
            url: '/api/hero/{id}',
            path: {
                'id': id,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @param id
     * @returns string
     * @throws ApiError
     */
    public heroControllerRemove(
        id: string,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'DELETE',
            url: '/api/hero/{id}',
            path: {
                'id': id,
            },
        });
    }

}
