import {Component, EventEmitter, Output} from '@angular/core';
import {MatDialog} from "@angular/material/dialog";
import {Skill} from "../../../../../api";
import {SkillFormComponent} from "../skill-form/skill-form.component";

@Component({
  selector: 'app-update-skill',
  templateUrl: './update-skill.component.html',
  styleUrls: ['./update-skill.component.scss']
})
export class UpdateSkillComponent {
  @Output()
  dataFromModal = new EventEmitter()


  constructor(public dialog: MatDialog
  ) {}

  public async openUpdateDialog(item: Skill): Promise<void> {
    this.dialog.open(SkillFormComponent, {
      data: {mode: 'update', item, emitter: this.dataFromModal},
      width: '70%'
    });
  }
}
