import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import type { Observable } from 'rxjs';

import type { CreateProfessionDto } from '../models/CreateProfessionDto';
import type { Profession } from '../models/Profession';
import type { UpdateProfessionDto } from '../models/UpdateProfessionDto';

import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

@Injectable()
export class ProfessionsService {

    constructor(public readonly http: HttpClient) {}

    /**
     * @param requestBody
     * @returns Profession
     * @throws ApiError
     */
    public professionControllerCreate(
        requestBody: CreateProfessionDto,
    ): Observable<Profession> {
        return __request(OpenAPI, this.http, {
            method: 'POST',
            url: '/api/profession',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns Profession
     * @throws ApiError
     */
    public professionControllerFindAll(): Observable<Array<Profession>> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/profession',
        });
    }

    /**
     * @param id
     * @returns Profession
     * @throws ApiError
     */
    public professionControllerFindOne(
        id: string,
    ): Observable<Profession> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/profession/{id}',
            path: {
                'id': id,
            },
        });
    }

    /**
     * @param id
     * @param requestBody
     * @returns Profession
     * @throws ApiError
     */
    public professionControllerUpdate(
        id: string,
        requestBody: UpdateProfessionDto,
    ): Observable<Profession> {
        return __request(OpenAPI, this.http, {
            method: 'PATCH',
            url: '/api/profession/{id}',
            path: {
                'id': id,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @param id
     * @returns any
     * @throws ApiError
     */
    public professionControllerRemove(
        id: string,
    ): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'DELETE',
            url: '/api/profession/{id}',
            path: {
                'id': id,
            },
        });
    }

}
