import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import type { Observable } from 'rxjs';

import type { CreateTalentDto } from '../models/CreateTalentDto';
import type { Talent } from '../models/Talent';
import type { UpdateTalentDto } from '../models/UpdateTalentDto';

import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

@Injectable()
export class TalentsService {

    constructor(public readonly http: HttpClient) {}

    /**
     * @param requestBody
     * @returns Talent
     * @throws ApiError
     */
    public talentControllerCreate(
        requestBody: CreateTalentDto,
    ): Observable<Talent> {
        return __request(OpenAPI, this.http, {
            method: 'POST',
            url: '/api/talent',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns Talent
     * @throws ApiError
     */
    public talentControllerFindAll(): Observable<Array<Talent>> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/talent',
        });
    }

    /**
     * @param id
     * @returns any
     * @throws ApiError
     */
    public talentControllerFindOne(
        id: string,
    ): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/talent/{id}',
            path: {
                'id': id,
            },
        });
    }

    /**
     * @param id
     * @param requestBody
     * @returns Talent
     * @throws ApiError
     */
    public talentControllerUpdate(
        id: string,
        requestBody: UpdateTalentDto,
    ): Observable<Talent> {
        return __request(OpenAPI, this.http, {
            method: 'PATCH',
            url: '/api/talent/{id}',
            path: {
                'id': id,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @param id
     * @returns any
     * @throws ApiError
     */
    public talentControllerRemove(
        id: string,
    ): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'DELETE',
            url: '/api/talent/{id}',
            path: {
                'id': id,
            },
        });
    }

}
