import { Pipe, PipeTransform } from '@angular/core';
import { User } from 'src/api';

@Pipe({
  name: 'hasPrivileges'
})
export class HasPrivilegesPipe implements PipeTransform {



  transform(value: User.role, requiredRole: string): boolean {
    return hasPrivileges(value, requiredRole as User.role)
  }

}

export function hasPrivileges(userRole: User.role, requiredRole: User.role) {
  console.log(userRole)
  console.log(requiredRole)
  const userRoleIndex = Object.values(User.role).indexOf(userRole);
  const requireRoleIndex = Object.values(User.role).indexOf(requiredRole);
  return userRoleIndex >= requireRoleIndex;

}
