import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import type { Observable } from 'rxjs';

import type { CreateSkillDto } from '../models/CreateSkillDto';
import type { Skill } from '../models/Skill';
import type { UpdateSkillDto } from '../models/UpdateSkillDto';

import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

@Injectable()
export class SkillsService {

    constructor(public readonly http: HttpClient) {}

    /**
     * @param requestBody
     * @returns Skill
     * @throws ApiError
     */
    public skillControllerCreate(
        requestBody: CreateSkillDto,
    ): Observable<Skill> {
        return __request(OpenAPI, this.http, {
            method: 'POST',
            url: '/api/skill',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns Skill
     * @throws ApiError
     */
    public skillControllerFindAll(): Observable<Array<Skill>> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/skill',
        });
    }

    /**
     * @returns string
     * @throws ApiError
     */
    public skillControllerTest(): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/skill/test',
        });
    }

    /**
     * @param id
     * @returns Skill
     * @throws ApiError
     */
    public skillControllerFindOne(
        id: string,
    ): Observable<Skill> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/skill/{id}',
            path: {
                'id': id,
            },
        });
    }

    /**
     * @param id
     * @param requestBody
     * @returns Skill
     * @throws ApiError
     */
    public skillControllerUpdate(
        id: string,
        requestBody: UpdateSkillDto,
    ): Observable<Skill> {
        return __request(OpenAPI, this.http, {
            method: 'PATCH',
            url: '/api/skill/{id}',
            path: {
                'id': id,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @param id
     * @returns any
     * @throws ApiError
     */
    public skillControllerRemove(
        id: string,
    ): Observable<any> {
        return __request(OpenAPI, this.http, {
            method: 'DELETE',
            url: '/api/skill/{id}',
            path: {
                'id': id,
            },
        });
    }

}
