import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import type { Observable } from 'rxjs';

import type { CreateItemDto } from '../models/CreateItemDto';
import type { UpdateItemDto } from '../models/UpdateItemDto';

import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

@Injectable()
export class ItemsService {

    constructor(public readonly http: HttpClient) {}

    /**
     * @param requestBody
     * @returns string
     * @throws ApiError
     */
    public itemControllerCreate(
        requestBody: CreateItemDto,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'POST',
            url: '/api/item',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns string
     * @throws ApiError
     */
    public itemControllerFindAll(): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/item',
        });
    }

    /**
     * @param id
     * @returns string
     * @throws ApiError
     */
    public itemControllerFindOne(
        id: string,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'GET',
            url: '/api/item/{id}',
            path: {
                'id': id,
            },
        });
    }

    /**
     * @param id
     * @param requestBody
     * @returns string
     * @throws ApiError
     */
    public itemControllerUpdate(
        id: string,
        requestBody: UpdateItemDto,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'PATCH',
            url: '/api/item/{id}',
            path: {
                'id': id,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @param id
     * @returns string
     * @throws ApiError
     */
    public itemControllerRemove(
        id: string,
    ): Observable<string> {
        return __request(OpenAPI, this.http, {
            method: 'DELETE',
            url: '/api/item/{id}',
            path: {
                'id': id,
            },
        });
    }

}
